@import url('https://fonts.googleapis.com/css?family=Sulphur+Point&display=swap');

*,
*::after,
*::before {
	box-sizing: border-box;
}

:root {
  font-size: 20px;
}

::selection {
  background: #2ddab8;
  color: white;
}

body {
  margin: 0;
  padding: 0;
  background: #0c0f13;
  overflow: hidden;
  font-family: 'Sulphur Point', sans-serif;
  color: #8b8b8b;
  font-size: 0.9rem;
}

a {
  color: #545454;
  text-decoration: none;
}

a:focus, 
a:hover {
  color: #8b8b8b;
}

#root {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
#we {
  position: absolute;
  justify-content: center;
  left: 50%;
  transform: translate(-50%, -50%);
  top: 405%;
  font-size: 24px;
  text-align: center;
  color: #2ddab8;
  font-weight: bold;
}
.loading {
  padding: 10px;
  transform: translate3d(-50%, -50%, 0);
  color: #8b8b8b;
}
#myId{
  width: 90%;
  height: 60%;
  position: absolute;
  justify-content: center;
  left: 50%;
  transform: translate(-50%, -50%);
  top: 450%;
  border: none;
  
}
#telegram-img{
  position: absolute;
    height: 30px;
    width: 30px;
    z-index: 2;
}
#telegram {
  position: absolute;
  justify-content: center;
  color: #2FE8C3;
  background: #0C0F13;
  border-style: solid;
  border-inline: #2FE8C3;
  left: 50%;
    top: 578%;
    height: 30px;
    z-index: 2;
    transform: translate(-50%, -50%);
}
.scrollArea {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: auto;
}
.pc {
  display: none;
}
.frame {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  padding: 1.5rem;
  z-index: 2;
}
.frame__nav{
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 50%;
  padding: 1.5rem;
  z-index: 1;
  transform: translate(-50%, 0%);
}
.frame__nav::after{
  content: '';
    position: absolute;
    height: 1px;
    width: calc(100% - 50px);
    right: 30px;
    top: 0;
    background: #2FE8C3;
    z-index: -1;
}
.frame__title {
  font-size: inherit;
  font-weight: normal;
  margin: 0;
}

.frame__links {
  margin: 1rem 0;
}

.frame__link:not(:last-child) {
  margin-right: 1rem;
}

.frame__nav .frame__link:first-child {
  margin-right: 1.5rem;
}

.bottom-left {
  padding: 1.5rem;
  transform: translate3d(0, -100%, 0) !important;
}

#appstore {
  position: absolute;
  justify-content: center;
  color: #2FE8C3;
  background: #0C0F13;
  border-style: solid;
  border-inline: #2FE8C3;
  left: 14%;
    top: 283%;
    height: 30px;
    z-index: 2;
    transform: translate(-50%, -50%);
}

#playstore {
  position: absolute;
  justify-content: center;
  color: #2FE8C3;
  background: #0C0F13;
  border-style: solid;
  border-inline: #2FE8C3;
  left: 40%;
    top: 283%;
    height: 30px;
    z-index: 2;
    transform: translate(-50%, -50%);
}

