@import url('https://fonts.googleapis.com/css?family=Sulphur+Point&display=swap');
:root {
    --primary: #2FE8C3;
}

.btn {
    font-family: 'Sulphur Point', sans-serif;
    font-weight: 400;
    border-radius: 0%;
    cursor: pointer;
    transition: transform 0.3 ease;
}

.btn:hover {
    transform:  translateY(-3px);
}

.btn--primary--solid {
    background-color: var(--primary);
    color: var(--white);
    border: none;
}

.btn--medium {
    padding: 10px 20px;
    font-size: 18px;
}

.btn--small {
    padding: 15px 30px;
    font-size: 18px;
}
